import Link from "next/link";
import { 
  Fish, 
  Waves, 
  Anchor, 
  Compass, 
  Ship,
  Settings,
  Package,
  Zap,
  Target,
  Scissors,
  Box,
  Boxes,
  Shirt,
  Backpack,
  Sun,
  Wind
} from "lucide-react";

const categories = [
  { icon: Fish, name: "Cần Câu", color: "#ee4d2d" },
  { icon: Waves, name: "Máy Câu", color: "#f05d40" },
  { icon: Anchor, name: "Dây Cước", color: "#1e9d74" },
  { icon: Compass, name: "Lưỡi Câu", color: "#0088ff" },
  { icon: Ship, name: "Mồi Câu", color: "#f69113" },
  { icon: Settings, name: "Phụ Kiện Cần", color: "#c61a1f" },
  { icon: Package, name: "Hộp Đựng Đồ", color: "#0cb35b" },
  { icon: Zap, name: "Máy Nổi Câu", color: "#8b37c9" },
  { icon: Target, name: "Chì Câu", color: "#ee4d2d" },
  { icon: Scissors, name: "Kéo & Dao", color: "#0088ff" },
  { icon: Box, name: "Giỏ Đựng Cá", color: "#f05d40" },
  { icon: Boxes, name: "Túi Đồ Nghề", color: "#1e9d74" },
  { icon: Shirt, name: "Quần Áo Câu", color: "#f69113" },
  { icon: Backpack, name: "Balo & Túi", color: "#c61a1f" },
  { icon: Sun, name: "Mũ & Kính", color: "#0cb35b" },
  { icon: Wind, name: "Dụng Cụ Khác", color: "#8b37c9" }
];

export function CategoryGrid() {
  return (
    <div className="container mx-auto px-4 py-4">
      <div className="bg-white rounded-lg shadow-sm p-6">
        <h2 className="text-gray-700 font-semibold text-lg mb-4">Danh Mục Sản Phẩm</h2>
        <div className="grid grid-cols-8 gap-4">
          {categories.map((category, index) => {
            const Icon = category.icon;
            return (
              <Link
                key={index}
                href={`/category/${index}` as any}
                className="flex flex-col items-center gap-3 p-4 hover:shadow-lg hover:scale-105 transition-all rounded-lg border border-gray-100 group"
              >
                <div className="w-14 h-14 flex items-center justify-center bg-orange-50 rounded-full group-hover:bg-orange-100 transition-colors">
                  <Icon 
                    className="w-8 h-8 group-hover:scale-110 transition-transform" 
                    style={{ color: category.color }}
                  />
                </div>
                <span className="text-xs text-center text-gray-700 line-clamp-2 font-medium">
                  {category.name}
                </span>
              </Link>
            );
          })}
        </div>
      </div>
    </div>
  );
}
